// Заголовки, общие БЄМ блоки и проче...

html,
body {
  min-width: 320px;
  width: 100%;
  font-family: var(--bs-font-family);
  background-color: var(--bs-body-bg);
  color: var(--bs-font-color);
  font-size: var(--bs-font-size);
  font-weight: var(--bs-font-weight);
  line-height: var(--bs-line-height);
}

main {
  & > section {
    &:first-child {
      padding-top: 26px 0;

      @media (min-width: 768px) {
        padding-top: 41px;
      }

      @media (min-width: 1200px) {
        padding-top: 56px;
      }
    }
  }
}

.section-padding {
  padding: 30px 0;

  @media (min-width: 768px) {
    padding: 45px 0;
  }

  @media (min-width: 1200px) {
    padding: 60px 0;
  }

}

.section-margin {
  margin: 15px 0;

  @media (min-width: 768px) {
    margin: 30px 0;
  }

  @media (min-width: 1200px) {
    margin: 45px 0;
  }
}

.section-title {
  font-weight: 400;
  font-size: 20px;
  margin-bottom: 0;

  @media (min-width: 768px) {
    font-size: 26px;
  }

  @media (min-width: 1200px) {
    font-size: 36px;
  }

  &.h1 {
    text-transform: uppercase;
    font-size: 32px;

    @media (min-width: 768px) {
      font-size: 44px;
    }

    @media (min-width: 1200px) {
      font-size: 64px;
    }
  }
}


.custom-checkbox {
  display: inline-block;
  position: relative;
  cursor: pointer;
  width: 23px;
  height: 23px;
  //padding-left: 30px; /* Расстояние между кастомным чекбоксом и текстом */

  input {
    display: none;
  }

  &::before {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    width: 23px;
    height: 23px;
    border: 2px solid #6489FF;
    background-color: transparent;
    border-radius: 4px;
  }

  &::after {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    color: #fff;
    font-size: 16px;
    visibility: hidden;
    background-image: url('../images/svg/check.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 15px;
    width: 100%;
    height: 100%;
  }
}

.custom-checkbox {
  &.active {
    &::before {

    }

    &::after {
      visibility: visible;
    }
  }
}

.btn {
  color: var(--btn-text);
  background-color: var(--btn-bg);
  border-radius: 54px;
  font-weight: 500;
  font-size: 16px;
  padding: 19px 24px;
  display: inline-flex;
  align-items: center;

  span {
    display: inline-block;
  }

  &:focus {
    outline: none;
    box-shadow: none;
    border: none;
  }

  &:hover {
    cursor: pointer;
    color: #fff;
    background-color: var(--btn-text);

    .btn__icon {
      background-color: rgba(255, 255, 255, 0.1);
      transition: all .2s ease;
    }
  }

  &.btn--icon {
    padding: 7px 8px 7px 24px;
  }

  &--darker {
    color: #fff;
    background-color: var(--btn-text);

    .btn__icon {
      background-color: rgba(255, 255, 255, 0.1);
    }

    &:hover {
      color: var(--btn-hover-text);
      background-color: var(--btn-hover-bg);

      .btn__icon {
        background-color: var(--btn-text);
        transition: all .2s ease;
      }
    }
  }
}

.btn__icon {
  min-width: 44px;
  min-height: 44px;
  max-width: 44px;
  max-height: 44px;
  background-color: var(--btn-text);
  margin-left: 7px;
  border-radius: 50%;
  display: block;
  position: relative;
  transition: all .2s ease;

  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    background-image: url('../img/icons/arrow-right.svg');
    background-repeat: no-repeat;
    background-position: center;
  }

  &--45deg {
    transform: rotate(-45deg);
  }

  &--empty {
    background-color: transparent;
    border: 1px solid var(--btn-text);

    &::before {
      background-image: url('../img/icons/arrow-right--accent.svg');
    }
  }
}

.accent {
  color: var(--primary-color);
}


main {
  display: block;
  overflow: hidden;

  &.isHeaderFixed {
    & > * {
      &:first-child {
        padding-top: 92px;

        @media (min-width: 992px) {
          padding-top: 92px;
        }
      }
    }
  }
}


.swiper {
  width: 100%;
  height: 100%;

  .swiper-button-next, .swiper-button-prev {
    width: 40px;
    height: 40px;
    background-color: rgba(219, 52, 31, 0.3);
    top: var(--swiper-navigation-top-offset, 47%);
    transition: all .1s ease-out;

    @media (min-width: 640px) {
      top: var(--swiper-navigation-top-offset, 45.5%);
    }

    @media (min-width: 810px) {
      top: var(--swiper-navigation-top-offset, 52.5%);
    }

    @media (min-width: 1400px) {
      width: 50px;
      height: 50px;
      top: var(--swiper-navigation-top-offset, 53%);
    }

    &::after {
      color: #fff;
      font-size: 16px;
      transition: all .1s ease-in;

      @media (min-width: 1400px) {
        font-size: 21px;
      }
    }

    &:active {
      background-color: rgba(84, 235, 255, 0.3);
      transform: scale3d(0.9,0.9,1);
      border-radius: 4px;
      transition: all .1s ease-in;
    }
  }

  .swiper-button-next {
    &:hover {
      &::after {
        transform: translateX(3px);
      }
    }

    @media (min-width: 1400px) {
      right: var(--swiper-navigation-sides-offset, 6px);
    }
  }

  .swiper-button-prev {
    &:hover {
      &::after {
        transform: translateX(-3px);
      }
    }

    @media (min-width: 1400px) {
      left: var(--swiper-navigation-sides-offset, 6px);
    }
  }

  .swiper-button-disabled {
    opacity: 0;
  }
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.container, .container-fluid:not(.container-fluid--wide) {
  @media (min-width: 1400px) {
    max-width: 1336px;
  }
}