/** Базовые анимации: */
@import 'keyframes';

/** Стили для полноэкранного блока: */
// @import "fullscreen";

/*
* (i) Стили будут применяться ко
* всем классам содержащим *__container
* Например header__container, main__container и т.д.
*/
[class*='__container'] {
  max-width: rem($maxWidthContainer);
  margin: 0 auto;
  padding: 0 rem($mainOffset);
}

// Стили отзывчивых картинок (IBG) ============================================
[class*='-ibg'] {
  position: relative;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}

[class*='-ibg_contain'] {
  img {
    object-fit: contain;
  }
}

// Оболочка ===================================================================
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;

  /** Поменять на clip - если используется в проекте position: sticky */
  overflow: hidden;
}

.page {
  position: relative;
  flex: 1 1 auto;

  [data-observ] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: rem(2);
    background: transparent;
    pointer-events: none;
  }

  &_start {
    padding-top: rem($headerHeight);
  }
}

.visually-hidden, /* - если убрать, то в IE11 лэйблы скачут */
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input {
  background-color: var(--bs-input-bg);
  color: var(--bs-input-text);
  padding: var(--bs-input-padding);
  border-radius: var(--bs-texarea-br);
  border: 1px solid rgba(255, 255, 255, 0.10);

  &::placeholder {
    color: var(--bs-btn-text-disabled);
  }
  &:focus {
    border-color: var(--bs-input-border);
  }
}

textarea {
  background-color: var(--bs-input-bg);
  color: var(--bs-input-text);
  padding: var(--bs-texarea-p-y) var(--bs-texarea-p-x);
  border-radius: var(--bs-texarea-br);
  border: 1px solid rgba(255, 255, 255, 0.10);

  &::placeholder {
    color: var(--bs-btn-text-disabled);
  }

  &:focus {
    border-color: var(--bs-input-border);
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}