$mainOffset: 15; // отступы безопасности для контейнера
$maxWidth: 1920; // максимальная ширина страницы
$minWidth: 320; // минимальная ширина телефона
$containerWidth: 1160; // ширина контейнера из макета
$maxWidthContainer: $containerWidth + ($mainOffset * 2); // ширина контейнера с учетом padding слева и справа

$headerHeight: 100;
$footerHeight: 100;



:root {
  --bs-font-family: 'Montserrat', sans-serif; // Семейство шрифта по умолчанию
  --bs-font-size: 16px; // Размер шрифта по умолчанию
  --bs-font-weight: 400; // Жирность шрифта по умолчанию
  --bs-line-height: 180%; // Базовая высота шрифта
  --bs-body-bg: #f4f6f9;
  --bs-font-color: #1c1c1c; // Цвет шрифта по умолчанию

  --primary-color: rgb(154, 134, 97);
  //--primary-color-hover: rgb(255, 26, 0);
  //--primary-color-05: rgba(219, 52, 31, 0.5);

  --text-dark: #212121;
  --text-light: #f5f5f5;

  --primary-hover-color: #fff;

  --btn-text: #9a8661;
  --btn-bg: rgba(154, 134, 97, 0.2);

  --btn-hover-text: #9a8661;
  --btn-hover-bg: rgba(154, 134, 97, 0.2);

  --footer-bg: #1d1f1b;

  //

  --card-bg: #edf0f4;
}