body {
  position: relative;

  &::before,
  &::after {
    position: absolute;
    content: '';
    background-repeat: no-repeat;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }

  &::before {
    top: 0;
    background-image: url('../img/bg/top-decor.png');
    background-position: top center;
  }

  &::after {
    bottom: 0;
    background-image: url('../img/bg/bottom-decor.png');
    background-position: bottom center;
  }

  & > * {
    position: relative;
    z-index: 2;
  }

  &.menu-open {
    header {
      background-color: var(--bs-body-bg);
      transition: all .15s ease;
    }
  }
}

.header {
  border: 1px solid #dadada;
  transition: all 1.75s ease;
  background-color: transparent;

  &.header--absolute {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 5;
  }

  .navbar {
    padding: 16px 0;

    @media (min-width: 992px) {
      padding: 24px;
    }

    .navbar-nav {
      flex-direction: row;
      flex-wrap: wrap;
      margin-top: 28px;

      @media (min-width: 992px) {
        margin-top: 0;
        //width: 100%;
        display: flex;
        align-items: center;
      }

      .nav-item {
        width: 50%;

        @media (min-width: 992px) {
          width: unset;
        }
        
        &:nth-child(odd) {
          text-align: left;
        }

        &:nth-child(even) {
          text-align: right;
        }

        .nav-link {
          padding: 3px 0;
          font-size: 15px;
          color: #717173;

          @media (min-width: 992px) {
            font-size: 20px;
            padding: 3px 15px;
          }

          @media (min-width: 1200px) {
            padding: 5px 16px;
          }
          
          &:hover {
            color: #1c1d1f;
          }
        }
        
        &.active {
          .nav-link {
            color: #1c1d1f;
          }
        }
      }
    }

    .btn {
      @media (min-width: 992px) {
        margin-left: 15px;
      }

      @media (min-width: 1200px) {
        margin-left: 40px;
      }
    }
  }
}

.navbar-brand {
  display: flex;

  & > * {
    &:first-child {
      margin-right: 5px;
    }
  }

  svg, path, rect {
    transition: all .2s ease;
  }

  &:hover {
    .navbar-brand__logo {
      rect {
        //fill: var(--primary-color-hover);
      }
    }

    .navbar-brand__text {
      path {
        //fill: var(--primary-color-hover);
      }
    }
  }

  .navbar-brand__logo-text {
    font-size: 20px;
    line-height: 92%;
    max-width: 112px;
    display: block;
    white-space: break-spaces;
    position: relative;
    top: 4px;
  }
}

.navbar-toggler {
  border: none !important;
  padding: 10px;
  position: relative;
  right: -10px;

  &:focus {
    border: none !important;
    box-shadow: none;
  }

  .toggler-icon {
    width: 28px;
    height: 4px;
    border-radius: 4px;
    background-color: var(--primary-color);
    display: block;
    transition: all 0.2s;
    position: relative;
  }

  .top-bar {
    transform: rotate(0);
  }

  .middle-bar {
    opacity: 1;
    filter: alpha(opacity=100);
    margin: 6px auto;
  }

  .bottom-bar {
    transform: rotate(0);
  }
}

.navbar-toggler {
  &[aria-expanded="true"] {
    .toggler-icon {
      //background-color: #fff;
      //width: 37px;
    }

    .top-bar {
      transform: translateX(25%) rotate(45deg);
      transform-origin: 10% 10%;
      top: 2.5px;
    }

    .middle-bar {
      opacity: 0;
      filter: alpha(opacity=0);
    }

    .bottom-bar {
      transform: translateX(25%) rotate(-45deg);
      transform-origin: 10% 90%;
      bottom: 2.5px;
    }
  }
}

// main-hero //

.main-hero {

}

.main-hero__title,
.main-hero__subtitle {
  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    flex-direction: row;
    gap: 24px;
    align-items: center;
  }

  .section-title {
    width: fit-content;
    flex-shrink: 0;
    margin-bottom: 0;
  }

  p {
    @media (min-width: 768px) {
      margin-bottom: 0;
    }

    &.main-hero__subtitle-additional {
      font-size: 14px;
      line-height: 120%;
      width: min-content;
    }
  }
}

.main-hero__subtitle {
  margin-top: 12px;
}

.main-hero__article {
  margin-top: 80px;

  @media (min-width: 768px) {
    display: flex;
    gap: 32px;
  }
}

.big-article {
  padding: 16px 20px 16px;
  border-radius: 48px;
  background: var(--card-bg);
}


.big-article__images {
  display: grid;
  grid-template-columns: 66% 34%;
  gap: 8px;
  width: 64%;
}

.big-article__image {
  border-radius: 32px;
  overflow: hidden;
  height: calc(206px / 2 - 4px);

  img {
    width: 100%;
    object-fit: cover;
    object-position: center;
    height: 100%;
  }
}

.big-article__image:nth-child(3n + 1) {
  grid-row: span 2;
  height: 206px;
}


.big-article__info {
  width: 36%;
}

.big-article__title {
  font-size: 24px;
  width: 95%;
}

.big-article__desc {
  font-size: 14px;
  line-height: 160%;
  height: 45px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.text-grey {
  color: #717173;
}

.link-decoration {
  position: relative;

  &::before {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1%;
    background-color: var(--btn-text);
    transition: all .2s ease;
  }

  &:hover {
    &::before {
      transform: translateY(3px);
    }
  }
}

// main-works //

.title-w-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;

  .section-title {
    width: 68%;
  }

  .btn {
    margin-left: 50px;
    max-height: 60px;
  }
}

.title-w-text {
  display: flex;
  align-items: center;
  gap: 58px;
  justify-content: space-between;
  margin-bottom: 24px;

  .section-title {
    max-width: 40%;
    flex-shrink: 0;
  }

  p {
    padding-right: 100px;
    margin: 0;
    font-size: 18px;
    line-height: 160%;
  }
}

.main-works__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.main-works__item {
  flex-basis: calc(50% - 16px);
  flex-grow: 1;
  height: 100%;
}

.mini-article {
  background: var(--card-bg);
  border-radius: 48px;
  padding: 16px;
  display: flex;
  align-items: center;

  &:hover {
    .mini-article__icon {
      background-color: var(--btn-text);

      &::before {
        background-image: url('../img/icons/arrow-right.svg');
      }
    }
  }
}

.mini-article__image {
  overflow: hidden;
  margin-right: 24px;
  width: 89px;
  height: 87px;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    border-radius: 32px;
    object-position: center;
    object-fit: cover;
  }
}

.mini-article__title {
  font-size: 24px;
  margin-bottom: 8px;
}

.mini-article__text {
  font-size: 14px;
  line-height: 160%;
  height: 45px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  max-width: 90%;
  margin-bottom: 0;
}

.mini-article__icon {
  margin-left: auto;
}

// main-projects //

.main-projects__wrapper {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;

  @media (min-width: 1400px) {
    display: grid;
    grid-template-columns: 50% 25% 25%;
  }
}

.project-item {
  background: var(--card-bg);
  border-radius: 48px;
  padding: 16px 24px;
  display: flex;
  flex-wrap: nowrap;
  gap: 32px;
  flex-grow: 1;

  @media (min-width: 1400px) {
    min-height: 494px;
  }
}

.project-item__image {
  height: 100%;
  overflow: hidden;
  border-radius: 32px;
  flex-grow: 1;
  min-width: 295px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.project-item__content {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex-grow: 1;
}

.project-item__cloud {
  border: 1px solid #9a8661;
  border-radius: 51px;
  padding: 6px 16px;
  font-size: 14px;
  color: var(--btn-text);
  max-width: fit-content;
  margin-bottom: 20px;
}

.project-item__info {
  display: flex;
  gap: 24px;
  margin: 54px 0 57px;
}

.project-item__value {
  font-weight: 600;
  font-size: 48px;
  color: #9a8661;
}

.project-item__type {
  font-size: 24px;
}

.project-item__gain {

}

.project-item__terms,
.project-item__gain {
  p {
    font-size: 12px;
    line-height: 140%;
    margin-bottom: 0;
  }
}

.project-item__title {
  font-size: 24px;
  margin-top: auto;
}

.project-item__text {
  font-size: 14px;
  line-height: 160%;
}

.project-item__link {
  color: var(--btn-text);
  max-width: fit-content;
}

.wide-image__image {
  height: 400px;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0 0 46px 3px rgba(44, 32, 32, 0.65);
}

// main-news //

.main-news {

}

.main-news__wrapper {
  display: grid;
  grid-template-columns: calc(50% - 8px) calc(25% - 8px) calc(25% - 16px);
  grid-auto-flow: dense;
  gap: 16px;
}

.main-news__item {
  width: 100%;
  height: 234px;

  &:nth-child(4n + 1) {
    grid-column: 1 / 2;
  }

  &:nth-child(4n + 2) {
    grid-column: 1 / 2;
  }

  &:nth-child(4n + 3),
  &:nth-child(4n + 4) {
    grid-row: span 2;
    height: calc(234px * 2 + 16px);
    flex-direction: column;
    padding: 16px;

    .news-item__image {
      margin: 0 0 24px;
      width: 100%;
    }

    .news-item__title,
    .news-item__text {
      max-width: 100%;
    }

    .news-item__text {
      -webkit-line-clamp: 5;
      margin-bottom: 16px;
    }

    .news-item__link-wrap {
      padding: 0 10px;
    }
  }
}

.news-item {
  background: var(--card-bg);
  border-radius: 48px;
  padding: 16px 24px 16px 16px;
  display: flex;
  align-items: center;

  &:hover {
    .news-item__icon {
      background-color: var(--btn-text);

      &::before {
        background-image: url('../img/icons/arrow-right.svg');
      }
    }
  }
}

.news-item__image {
  overflow: hidden;
  margin-right: 24px;
  width: 202px;
  height: 202px;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    border-radius: 32px;
    object-position: center;
    object-fit: cover;
  }
}

.news-item__info {
  height: 100%;
  flex-grow: 1;
}

.news-item__title {
  font-size: 18px;
  margin-bottom: 8px;
  max-width: 95%;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: 122%;
}

.news-item__text {
  font-size: 14px;
  line-height: 160%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  max-width: 85%;
  margin-bottom: 0;
}

.news-item__link-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.news-item__link {
  line-height: 120%;
}

// partners //

.partners {

}

.partners__wrapper {
  margin-top: 32px;
  gap: 16px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

.partners__item {

}

.partner {
  padding: 32px;
  background: var(--card-bg);
  border-radius: 48px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.partner__image {
  text-align: center;

  img {
    margin: 0 auto;
    object-fit: contain;
    object-position: center;
    pointer-events: none;
    user-select: none;
  }
}

// footer //

.footer__plate {
  background: var(--footer-bg);
  border-radius: 62px;
  padding: 16px 16px 16px 39px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.footer__logo {
  .navbar-brand__logo-text {
    color: #fff;
  }
}

.footer__policy {
  text-align: center;
  display: flex;
  align-items: center;

  a {
    max-width: fit-content;
    margin: 0 auto;
    line-height: 122%;
  }
}

.footer__socials {}

.footer__social-items {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: flex-end;
}

.footer__social-item {
  a {
    width: 49px;
    height: 49px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    transition: all .2s ease;
    display: flex;
    align-items: center;
    text-align: center;

    img, svg {
      margin: 0 auto;
    }

    path {
      fill: var(--btn-text);
      transition: all .2s ease;
    }

    &:hover, &.active {
      background-color: var(--btn-text);
      border-color: var(--btn-text);

      path {
        fill: var(--footer-bg);
      }
    }
  }
}

.footer__copyright {
  padding: 20px;
  text-align: center;

  p {
    margin-bottom: 0;
  }
}